import { useState, useEffect } from 'react';
import Notification from './components/Notification';
import Note from './components/Note';
import Footer from './components/Footer';
import noteService from './services/notes';

const App = () => {
  const [notes, setNotes] = useState([]);
  const [newNote, setNewNote] = useState('');
  const [showAll, setShowAll] = useState(true);
  const [errorMessage, setErrorMessage] = useState(null);
 
  useEffect(() => {
    (async () => {
      try {
        const notes = await noteService.getAll();
        setNotes(notes);
      } catch (error) {
        alert(`The notes failed to load: ${error} ${error.message}`);
      }
    })();
  }, []);

  const addNote = async (event) => {
    event.preventDefault();
  
    const noteObject = {
      content: newNote,
      important: Math.random() < 0.5,
      id: notes.length + 1,
    };
  
    try {
      const newNote = await noteService.create(noteObject);
      setNotes(notes.concat(newNote));
      setNewNote('');
    } catch (error) {
      alert(`Failed to add new note: ${error} ${error.message}`);
    }
  };
  

  const handleNoteChange = (event) => {
    setNewNote(event.target.value);
  };

  const toggleImportanceOf = async (id) => {
    const note = notes.find(note => note.id === id);
  
    try {
      const changedNote = await noteService.update(id, {...note, important: !note.important });
      setNotes(notes.map(note => note.id !== id ? note : changedNote));
    } catch (error) {
      setErrorMessage(
        `Note '${note.content}' was already removed from the server`
      )
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000);
      setNotes(notes.filter(note => note.id !== id));
    }
  };
  

  const notesToShow = (showAll ? notes : notes.filter(note => note.important )) ;

  return (
    <div>
      <h1>Notes</h1>
      <Notification message={errorMessage} />
      <div>
        <button onClick={() => setShowAll(!showAll)}>
          show {showAll? 'important' : 'all' }
        </button>
      </div>
      <ul>
        {notesToShow.map(note => 
          <Note 
            key={note.id} 
            note={note} 
            toggleImportance={() => toggleImportanceOf(note.id)}/>
        )}
      </ul>
      <form onSubmit={addNote}>
        <input value={newNote} onChange={handleNoteChange} />
        <button type="submit">save</button>
      </form>
      <Footer />
    </div>
  );
};

export default App;
